/* Global Styles */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f4f4;
}

/* Navbar Styling */
nav {
  background-color: #003893; /* Blue from Colombian flag */
  padding: 10px 0;
  position: sticky;
  top: 0;
  z-index: 10;
}

.nav-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 80%;
  margin: 0 auto;
}

.nav-links {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.nav-links li {
  margin-left: 20px;
}

.nav-links a {
  color: white;
  text-decoration: none;
  font-weight: bold;
}

.nav-links a:hover {
  color: #FCD116; /* Yellow from Colombian flag */
}

/* Hero Section Styling */
.hero {
  background-color: #FCD116; /* Yellow from Colombian flag */
  background-size: cover;
  background-position: center;
  height: 100vh; /* Full viewport height */
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: white;
}

.hero-content {
  padding: 20px;
}

.hero h1 {
  font-size: 3rem;
  margin-bottom: 10px;
}

.hero .accent {
  font-size: 1.5rem;
  font-weight: bold;
}

/* Main Content Styling */
main {
  padding: 50px 0;
  text-align: center;
}

section {
  margin: 50px 0;
}

footer {
  background-color: #f30000; /* Blue from Colombian flag */
  color: white;
  text-align: center;
  padding: 20px;
}
