:root {
  --yellow: #FCD116;
  --blue: #003893;
  --red: #CE1126;
}

body {
  font-family: 'Arial, sans-serif';
  background: var(--yellow);
  color: #fff;
  margin: 0;
}

header {
  background: var(--blue);
  padding: 1rem;
  text-align: center;
}
 footer

 {
  background: var(--red);
  padding: 1rem;
  text-align: center;
}
.accent {
  color: var(--red);
}
.hero {
  background-image: 'url(/Public/Backgroundtest.jpg)';
  background-size: cover;
  background-position: center;
  padding: 100px 0;
  color: white;
  text-align: center;
}